@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,700;1,600&display=swap);

header {
  width: 100%;
  position: relative;
}

header.open {
  position: fixed;
}

nav {
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-toggle {
  color: #fff;
  font-size: 2.2rem;
  cursor: pointer;
  z-index: 1500;
}

.iconClose {
  display: none;
}

.nav-list {
  list-style: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 80%;
  height: 100vh;
  background-color: #fff;
  padding: 4.4rem;
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
  z-index: 1250;
  transform: translateX(-100%);
  transition: transform 0.5s;
}

.nav::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1000;
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.5s;
}

.iconBars,
.iconClose,
.menu-right img {
  width: 1.925rem;
}

.open .iconClose {
  display: block;
}

.open .iconBars {
  display: none;
}

.open .nav-list {
  transform: translateX(0);
}

.open .nav::before {
  opacity: 1;
  transform: scale(1);
}

.nav-item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.8);
  padding: 1rem 0;
}

.nav-link {
  display: block;
  color: var(--main-font-color);
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-weight: 600;
  transition: color 0.5s;
}

.nav-link:hover {
  color: var(--secondary-font-color);
}

.menu-right {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.menu-right li {
  position: relative;
}

.menu-right li:last-child img {
  display: none;
}

.topBar {
  background-color: var(--background-color-grey);
  height: 3.5rem;
}

.topBar .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.topBar .column {
  width: 100%;
}

.btnLang {
  background-color: transparent;
  border: none;
  outline: 0;
  font-family: var(--main-font);
  cursor: pointer;
  font-size: 1.2rem;
}

.btnLang img {
  width: 10px;
  transition: transform 0.5s ease;
}

.btnLang img.flip {
  transform: rotate(180deg);
  transition: transform 0.5s ease;
}

.langList {
  font-size: 1.2rem;
  min-width: 10.2rem;
  min-height: 5.8rem;
  list-style: none;
  box-shadow: 5px 5px 30px rgb(0, 0, 0, 0.05);
  padding: 1rem 1.5rem;
  position: absolute;
  background: #fff;
  z-index: 2000;
  display: none;
}

.langList.show {
  display: block;
}

.langList li:hover {
  cursor: pointer;
  font-weight: 500;
}

.langList li:first-child {
  border-bottom: 1px solid rgb(0, 0, 0, 0.2);
  padding-bottom: 5px;
  margin-bottom: 5px;
}

.fs-note {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fs-note img {
  width: 20px;
  margin-right: 1rem;
}

.right-list {
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.right-list li {
  margin-left: 2.5rem;
}

.right-list li:first-child {
  margin-left: 0;
}

.userOnMobile img {
  width: 35px;
  height: 35px;
  margin: 4rem 10px 4rem 0;
}

.bag-count {
  background-color: #ff0000;
  color: #fff;
  width: 2rem;
  height: 2rem;
  text-align: center;
  border-radius: 50%;
  line-height: 1.75;
  position: absolute;
  top: -5px;
  right: -10px;
  font-weight: 500;
}

footer {
  padding: 2rem;
  background-color: var(--background-color-grey);
  text-align: center;
  text-transform: uppercase;
}

.product-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
}

.product-item {
  margin-bottom: 3rem;
}

.product-img {
  width: 100%;
  height: 35rem;
  background-color: #f6f6f6;
  padding: 4rem 0 1.5rem;
  overflow: hidden;
  cursor: pointer;
}

.product-img img {
  transition: transform 0.4s ease;
}

.product-img:hover img {
  transform: translateY(-30px);
  transition: transform 0.5s ease;
}

.product-detail {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 0 1rem;
}

.product-detail .name {
  font-weight: 500;
  font-size: 1.8rem;
  text-transform: uppercase;
  cursor: pointer;
}

.product-detail .price {
  font-weight: 400;
  font-size: 1.8rem;
  color: var(--secondary-font-color);
}

.product-category {
  font-size: 1.4rem;
  color: var(--secondary-font-color);
}

@media (max-width: 1000px) {
  .product-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 767px) {
  .product-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .product-list {
    grid-template-columns: repeat(1, 1fr);
  }

  .product-item {
    width: auto;
    width: initial;
    margin-bottom: 2rem;
  }
}

.size-type {
  width: 4.8rem;
  height: 4.8rem;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  color: #fff;
  background-color: #000;
  cursor: pointer;
  transition: all 0.5s ease;
}

.size-type.unselected {
  border: 1px solid rgb(19, 18, 18, 0.1);
  color: #000;
  color: initial;
  background-color: transparent;
  background-color: initial;
}

.color-type {
  width: 4.8rem;
  height: 4.8rem;
  border: 5px solid #000;
  border-radius: 50%;
  cursor: pointer;
}

.color-type.unselected {
  border: none;
}

.modal {
  position: fixed;
  overflow: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.65);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5em 1em;
  z-index: 999999;
  box-sizing: border-box;
}

.modal.modal-fade {
  animation: fade-in 1s 1 linear;
  animation-fill-mode: forwards;
  opacity: 0;
}

.modal > .modal-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.modal > .modal-close {
  position: absolute;
  right: 15px;
  top: 10px;
  color: #5e5e5e;
  cursor: pointer;
  font-size: 1.25em;
  padding: 7px;
  background: rgba(255, 255, 255, 0.749);
  border: 1px solid #c3c0c0;
  border-radius: 50%;
  width: 42px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999999;
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
}

.modal > .modal-close:hover {
  background: rgba(255, 255, 255, 0.989);
}

.modal > .modal-body {
  z-index: 2;
  position: relative;
  margin: 0 auto;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 100%;
  color: #c3c0c0;
}

@keyframes fade-in {
  0% {
    animation-timing-function: cubic-bezier(0.2242, 0.7499, 0.3142, 0.8148);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.p-detail {
  display: grid;
  grid-template-columns: 42% 52%;
  grid-gap: 6%;
}

.p-size ul,
.p-color ul,
.p-small-imgs {
  list-style: none;
}

.p-category {
  font-weight: 400;
  text-transform: uppercase;
  font-size: 1.6rem;
}

.p-name {
  text-align: left;
  text-align: initial;
  margin-left: -1px;
  margin-bottom: 0.11rem;
}

.p-about {
  font-size: 1.8rem;
  line-height: 3rem;
  color: var(--secondary-font-color);
}

.p-video {
  margin: 2rem 0 3.8rem;
  display: flex;
  align-items: center;
  transition: transform 0.5s ease;
}

.p-video:hover {
  cursor: pointer;
  transform: translateX(10px);
  transition: transform 0.5s ease;
}

.p-video p {
  font-size: 2rem;
}

.play-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5.6rem;
  height: 5.6rem;
  background-color: rgba(19, 18, 18, 0.05);
  border-radius: 50%;
  margin-right: 2rem;
}

.play-btn img {
  width: 14px;
  margin-left: 4px;
}

.p-size ul,
.p-color ul,
.size-selector,
.color-selector {
  display: grid;
  grid-template-columns: repeat(5, 4.8rem);
  grid-gap: 2rem;
  margin-top: 1.8rem;
  margin-bottom: 3.8rem;
}

.p-size p,
.p-color p {
  font-weight: 500;
  font-size: 1.8rem;
}

.p-size li {
  width: 4.8rem;
  height: 4.8rem;
  border: 1px solid rgb(19, 18, 18, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
}

.p-color li {
  width: 4.8rem;
  height: 4.8rem;
  border: 1px solid rgb(19, 18, 18, 0.1);
  border-radius: 50%;
}

.p-big-img {
  background-color: #f6f6f6;
}

.p-small-imgs {
  margin-top: 2rem;
  display: flex;
}

.p-small-imgs li {
  margin-right: 2rem;
}

.p-small-imgs img {
  width: 10rem;
  height: 10rem;
  object-fit: cover;
}

.add-to-bag {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f6f6f6;
  padding: 2rem 4rem;
  margin-top: 4rem;
}

.add-btn {
  width: 25.9rem;
  padding: 1.4rem 2.4rem;
  background-color: #000;
  color: #fff;
  border-radius: 2px;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.6rem;
  transition: transform 0.5s ease;
}

.add-btn:hover {
  cursor: pointer;
  transform: translateX(10px);
  transition: transform 0.5s ease;
}

.add-btn img,
.pay-btn img {
  width: 2rem;
}

.fs-note.left {
  justify-content: flex-start;
}

.video-container {
  position: relative;
  padding-bottom: 50%;
  /* padding-top: 30px; */
  width: 70vw;
  height: 0;
  overflow: hidden;
}
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 899px) {
  .p-detail {
    grid-template-columns: 100%;
    grid-gap: initial;
  }

  .p-small-imgs li {
    margin-right: 1.5rem;
  }

  .p-small-imgs img {
    width: 7rem;
    height: 7rem;
  }

  .p-small-imgs {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .p-name {
    margin-left: -2px;
    margin-bottom: 1.5rem;
    margin-top: 1rem;
  }

  .add-to-bag {
    padding: 0 0 2rem;
    margin-top: 0;
    flex-direction: column-reverse;
  }

  .add-btn {
    width: 100%;
    margin-bottom: 2rem;
    border-radius: 0;
  }

  .video-container {
    width: 100vw;
  }
}

.title-with-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5rem;
}

.title-with-icon h1 {
  margin-bottom: 0;
  margin-right: 2.6rem;
}

.bag-on-title {
  position: relative;
  z-index: -1;
}

.bag-on-title img {
  width: 3.7rem;
}

.bag-on-title .bag-count {
  top: -5px;
  right: -10px;
}

.table-head {
  display: grid;
  grid-template-columns: var(--two-column-table-template);
  grid-gap: var(--column-table-gap);
  background-color: #fafafa;
  font-size: 1.2rem;
  color: rgb(0, 0, 0, 0.6);
  font-weight: 500;
  margin-bottom: 2rem;
}

.table-body {
  display: grid;
  grid-template-columns: var(--two-column-table-template);
  grid-gap: var(--column-table-gap);
}

.table-body .column-2 {
  display: grid;
  grid-template-columns: var(--two-column-table-template);
  grid-gap: var(--column-table-gap);
  align-items: center;
}

.column-10 {
  display: grid;
  grid-template-columns: var(--four-column-table-template);
  grid-gap: var(--column-table-gap);
  padding: 1.5rem;
}

.table-body .column-10 {
  align-items: center;
}

.remove-btn {
  width: 20px;
  height: 20px;
  background: #000;
  color: #fff;
  text-align: center;
  border-radius: 50%;
  font-weight: 700;
}

.remove-btn:hover {
  cursor: pointer;
  background-color: #ff0000;
}

.item-name {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.6rem;
  margin-bottom: 1.3rem;
}

.item-detail {
  font-size: 1.6rem;
  color: rgb(0, 0, 0, 0.8);
  display: flex;
}

.item-detail .color {
  display: flex;
  align-items: center;
  margin-left: 2rem;
}

.color-box {
  width: 20px;
  height: 20px;
  margin-left: 1rem;
}

.item-price,
.item-qty,
.item-sub-total {
  font-size: 1.8rem;
  color: var(--secondary-font-color);
}

.item-qty {
  background-color: #fafafa;
  width: 6.6rem;
  text-align: center;
  padding: 0.5rem 0;
}

.item-sub-total {
  font-weight: 500;
}

.table-foot {
  display: flex;
  justify-content: flex-end;
}

.foot-container {
  width: 40rem;
}

.total-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fafafa;
  padding: 1.5rem 2rem;
}

p.bold {
  font-weight: 500;
}

.pay-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  background-color: #000;
  color: #fff;
  padding: 1.5rem 2rem;
  margin-top: 2rem;
}

.pay-btn p {
  font-weight: 700;
}

.on-mobile-only {
  display: none;
}

.find-btn {
  width: 30rem;
  margin: 0 auto;
  cursor: pointer;
}

/*Global styles*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --main-font: "Poppins", sans-serif;
  --main-font-color: #131212;
  --secondary-font-color: rgb(0, 0, 0, 0.7);
  --background-color-grey: #f5f5f5;
  --two-column-table-template: 18% 80%;
  --four-column-table-template: 38% 20% 20% 20%;
  --column-table-gap: 2%;
}

html {
  font-family: "Poppins", sans-serif;
  font-family: var(--main-font);
  font-size: 10px;
  color: #131212;
  color: var(--main-font-color);
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  font-size: 1.2rem;
}

section {
  padding: 5rem 0;
  overflow: hidden;
  min-height: 78vh;
}

img {
  width: 100%;
  max-width: 100%;
}

a {
  text-decoration: none;
}

p {
  font-size: 1.6rem;
}

.container {
  width: 100%;
  max-width: 124rem;
  margin: 0 auto;
  padding: 0 2.4rem;
}

h1 {
  font-size: 5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 7rem;
}

.loader-container {
  min-height: 100vh;
}

.loader {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 5px solid rgba(0, 0, 0, 0.2);
  border-left: 5px solid #000000;
  border-radius: 50%;
  animation: load8 1.1s infinite linear;
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Media queries */
@media screen and (min-width: 900px) {
  .menu-toggle {
    display: none;
  }

  .nav-list {
    position: static;
    position: initial;
    width: auto;
    width: initial;
    height: auto;
    height: initial;
    background-color: transparent;
    padding: 0;
    justify-content: initial;
    flex-direction: row;
    transform: none;
    transform: initial;
    transition: none 0s ease 0s;
    transition: initial;
  }

  .nav-item {
    margin: 0 1.5rem;
    border: none;
  }

  .nav-item:last-child {
    margin-right: 0;
  }

  .nav-link {
    color: #131212;
    color: var(--main-font-color);
    text-transform: capitalize;
    position: relative;
  }

  .userOnMobile {
    display: none;
  }

  .active {
    position: relative;
  }

  .active::before,
  .nav-link:hover::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #131212;
    background-color: var(--main-font-color);
    left: 0;
    bottom: -10px;
    animation: grow 0.5s forwards;
  }

  .menu-right li:last-child img {
    display: inline;
    display: initial;
    width: 22px;
    height: 23px;
  }

  .menu-right {
    width: 58px;
  }
}

@media (max-width: 889px) {
  .topBar {
    position: fixed;
    height: 150px;
    z-index: 2500;
    width: 80%;
    left: 0;
    top: 70vh;
    padding: 20px 4.4rem;
    opacity: 0;
    transform: translateX(-100%);
    transition: transform 0.5s;
  }

  .open .topBar {
    transform: translateX(0);
    opacity: 1;
  }

  .topBar .container {
    padding: 0;
    flex-direction: column;
  }

  .fs-note,
  .right-list {
    justify-content: initial;
  }

  h1 {
    line-height: 1;
    font-size: 3.5rem;
  }
}

@media (max-width: 600px) {
  :root {
    --two-column-table-template: 50% 50%;
    --four-column-table-template: repeat(1, 1fr);
    --column-table-gap: initial;
  }

  .table-body .column-2 {
    grid-template-columns: 20% 80%;
  }

  .remove-btn {
    margin-left: 0;
  }

  .item-detail {
    display: inline;
    display: initial;
    font-size: 1.4rem;
  }

  .item-detail .color {
    margin-left: 0;
  }

  .item-name {
    margin-bottom: 1rem;
    line-height: 1.25;
  }

  .item-price,
  .item-qty,
  .item-sub-total {
    font-size: 1.4rem;
  }

  .table-head {
    display: none;
  }

  .item-sub-total {
    justify-self: flex-end;
    font-size: 1.6rem;
  }

  .table-body {
    border: 1px solid rgb(0, 0, 0, 0.1);
    margin-bottom: 2rem;
    padding: 1rem 0.5rem 1rem 1rem;
  }

  .on-mobile-only {
    display: inline;
    display: initial;
  }

  .find-btn {
    width: 100%;
  }
}

@keyframes grow {
  0% {
    width: 0;
  }
  100% {
    opacity: 1;
    width: 100%;
  }
}

